@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    @include m.headings();
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}
.bold{
    font-weight: v.$bold;
}


[class$="__heading"]{
    @include m.nombre-pagina;
}
[class*="__logo"]{
    @include m.logo;


   
}
[class$="--black"]{
    color: v.$grisOscuro;
}

[class$="--white"]{
    color: v.$blanco;
}
[class$="__descripcion"]{
    @include m.descripcion-pagina;
}


